export default {
	data() {
		return {
			order: {},
			ordersn: "",
			value: ""
		}
	},
	onLoad() {
		try {
		    this.value = uni.getStorageSync('remsg');
			// const ordersn = uni.getStorageSync('ordersn');
			/* console.log('value:', value);
			console.log('ordersn:', ordersn); */
		    if (this.value) {
		        this.order = this.value
				this.ordersn = this.value.order_sn
				// console.log('order:', this.order);
		    }
		} catch (e) {
		    console.log('error: ', e);
		}
	},
	methods: {
		async __init() {
			// 调用退单接口
			this.$H.post("/agent/", {
				user_id: "100003",
				token: "dXQyMDIwMDMyMzExMjM0OTMzNzM3ODAz",
				opt: "order_refund",
				order_sn: this.ordersn, //始值
			}).then((res) => {
				if(res) {
					uni.showToast({
						title: "退单成功"
					})
					uni.navigateBack()
				}
			}).catch((e) => {
				console.log("catch error!!", e);
			})
		},
		refund() {
			// this.ordersn = this.value.order_sn
			this.__init()
		}
	}
}
